import styled from 'styled-components'

export const DetailWrapper = styled.div`
   width: 920px;
   margin: 0 auto;
`;
export const Header = styled.div`
   padding: 15px 0 15px 20px;
   color: #333;
   font-size: 34px;
   font-weight: 700;
`;
export const Content = styled.div`
color: ##2f2f2f;
	p {
		margin: 25px 0;
		font-size: 16px;
		line-height: 30px;
	}
	b {
		font-weight: bold;
	}
`;
export const Img = styled.img`
	width: 100%;
	height: 400px;
`;